.bg-login-admin{
    background-color: #0093E9;
    background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 33%, #ffffff 100%);
    width: 100%;
    min-height: 100vh;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 15px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.card{
    box-shadow: 0px 0px 14px -6px rgba(0,0,0,0.75);
    border-radius: 10px;
    .card-body{
        .logo{
            width: 150px;
            height: auto;
            align-items: center;
        }
    }
}
.btn-login{
    width: 60%;
    margin: 0 auto;
    background-color: #dfdfdf;
    color: #fff;
    border-radius: 10px;
}